.header {
  @include clearfix;
  background:                           white;
  display:                              flex;
  line-height:                          24px;
  position:                             relative;
  text-align:                           center;
  z-index:                              2;
  .container {
    align-items:                        stretch;
    display:                            flex;
    width:                              100%; }
  // Modifiers
  &.has-shadow {
    box-shadow:                         0 1px 2px rgba(black, 0.1); }
  // Responsiveness
  @include mobile {
    .container {
      flex-direction:                   column; } }
  @include tablet {
    height:                             $header-height; }
  &-sticky {
    position:                           fixed;
    width:                              100%;
    transition:                         all $speed $easing;
    &-scrolled {
      height:                           $header-height-scrolled;
      .header-item {
        color:                          $text; } } }

  .header-social {
    display:                            flex;
    margin-left:                        80px;
    .header-item {
      &:hover {
        color:                          $primary;
        border-bottom-color:            transparent;
        background:                     transparent;
        &:before {
          transform:                    rotate(45deg); }
        &:after {
          transform:                    rotate(-45deg); } }
      .fa {
        line-height:                    44px; } } }
  &-sticky-scrolled {
    .header-social {
      .header-item {
        .fa {
          line-height:                  24px; } } } } }

.header-toggle {
  @extend .hamburger;
  position:                             absolute;
  right:                                0;
  top:                                  0;
  // Responsiveness
  @include tablet {
    display:                            none; } }

// Elements
.header-item, .logo {
  align-items:                          center;
  display:                              flex;
  padding:                              10px;
  margin:                               2px;
  text-transform:                       uppercase;
  font-size:                            11px;
  font-weight:                          bold;
  a {
    flex-grow:                          1; }
  img {
    max-height:                         24px; }
  .button + .button {
    margin-left:                        10px; }
  .fa {
    font-size:                          21px;
    line-height:                        24px; }
  .tag {
    &:first-child {
      margin-right:                     5px; }
    &:last-child {
      margin-left:                      5px; } }
  // Responsiveness
  @include mobile {
    text-align:                         left; } }
.logo {
  margin-right:                         30px;
  border-bottom:                        2px solid transparent; }

.header-item a, a.header-item {
  color:                                white;
  border-bottom:                        2px solid transparent;
  &:hover, &.is-active {
    border-bottom:                      2px solid $primary; }
  &:hover, &.is-active:hover {
    color:                              white;
    background-color:                   $primary; }
  // Modifiers
  &.button {
    border-bottom-width:                1px;
    text-transform:                     none; } }

.header-icon {
  @include fa(14px, 24px);
  color:                                $text;
  margin:                               0 5px;
  &:hover {
    color:                              $link-hover; } }

.header-tab {
  align-items:                          center;
  border-bottom:                        1px solid transparent;
  color:                                $text;
  display:                              block;
  height:                               $header-height;
  line-height:                          24px;
  padding:                              13px 15px;
  &:hover {
    border-bottom:                      1px solid $link; }
  // Modifiers
  &.is-active {
    border-bottom:                      3px solid $link;
    color:                              $link; } }

// Containers
.header-left {
  align-items:                          stretch;
  display:                              flex;
  flex:                                 1;
  overflow:                             hidden;
  overflow-x:                           auto;
  white-space:                          nowrap;
  // Responsiveness
  @include mobile {
    height:                             $header-height; }
  @include desktop {
    margin-left:                        -10px; } }

.header-right {
  align-items:                          stretch;
  // Responsiveness
  @include tablet {
    display:                            flex; }
  @include desktop {
    margin-right:                       -10px; } }

.header-full {
  align-items:                          stretch;
  display:                              flex;
  justify-content:                      center;
  text-align:                           center;
  width:                                100%;
  & > .header-item {
    align-items:                        stretch;
    display:                            flex;
    flex:                               1;
    justify-content:                    center;
    padding:                            0;
    & > a {
      align-items:                      center;
      display:                          flex;
      justify-content:                  center;
      width:                            100%; } } }

.header-menu {
  // Responsiveness
  @include mobile {
    box-shadow:                         0 4px 7px rgba(black, 0.1);
    display:                            none;
    .header-item {
      border-top:                       1px solid rgba($border, 0.5);
      padding:                          10px; }
    // Modifiers
    &.is-active {
      display:                          block; } } }

// States
.header.is-centered {
  justify-content:                      center;
  .header-left, .header-right {
    justify-content:                    center; } }

.header.is-small {
  background:                           $background;
  box-shadow:                           none;
  height:                               40px;
  z-index:                              1;
  .container {
    height:                             40px; }
  .header-tab {
    font-size:                          13px;
    height:                             40px;
    padding:                            8px 10px;
    &:hover, &.is-active {
      border-bottom-width:              2px; } } }
