.table {
  background:                           white;
  color:                                $text-strong;
  margin-bottom:                        20px;
  width:                                100%;
  td, th {
    border:                             1px solid $border;
    border-width:                       0 0 1px;
    padding:                            8px 10px;
    vertical-align:                     top;
    // Modifiers
    &.table-icon {
      padding:                          5px;
      text-align:                       center;
      white-space:                      nowrap;
      width:                            1%;
      .fa {
        @include fa(21px, 24px); }
      &.table-link {
        padding:                        0;
        & > a {
          padding:                      5px; } } }
    &.table-link {
      padding:                          0;
      & > a {
        display:                        block;
        padding:                        8px 10px;
        &:hover {
          background:                   $link;
          color:                        $link-invert; } } }
    &.table-narrow {
      white-space:                      nowrap;
      width:                            1%; } }
  th {
    color:                              $text-strong;
    text-align:                         left; }
  tr {
    &:hover {
      background:                       $background;
      color:                            $text-strong; } }
  thead {
    td, th {
      border-width:                     0 0 2px;
      color:                            $text-light; } }
  tbody {
    tr {
      &:last-child {
        td, th {
          border-bottom-width:          0; } } } }
  tfoot {
    td, th {
      border-width:                     2px 0 0;
      color:                            $text-light; } }
  // Modifiers
  &.is-bordered {
    td, th {
      border-width:                     1px; }
    tr {
      &:last-child {
        td, th {
          border-bottom-width:          1px; } } } }
  &.is-narrow {
    td, th {
      padding:                          5px 10px;
      // Modifiers
      &.table-icon {
        padding:                        2px;
        &.table-link {
          padding:                      0;
          & > a {
            padding:                    2px; } } }
      &.table-link {
        padding:                        0;
        & > a {
          padding:                      5px 10px; } } } }
  &.is-striped {
    tbody {
      tr {
        &:nth-child(2n) {
          background:                   $background;
          &:hover {
            background:                 $border; } } } } } }
